@tailwind base;html,body{font-size:16px;-webkit-text-size-adjust:100%;height:100%;font-family:system-ui,BlinkMacSystemFont,-apple-system,"Segoe UI","Roboto","Oxygen","Ubuntu","Cantarell","Fira Sans","Droid Sans","Helvetica Neue",sans-serif}body{margin:0;padding:0;background-color:#f3f7fc;color:#3f4d5a;display:flex}h1{margin-top:0}h2{margin-top:24px;font-size:1em}h2:first-child{margin-top:0}p{line-height:1.4em;margin-bottom:1.4em}ul{line-height:1.3em;padding-left:20px;margin-bottom:0}ul li{margin-bottom:.35em}a{color:#0b69a3;text-decoration:none}a:hover{text-decoration:underline}.go{color:#0b69a3}.go:after{padding-left:4px;content:"→";text-decoration:none !important}small{color:#8b96a2}code{display:inline-block;color:#ef4e4e;padding:0 2px;background:#f3f7fc;border-radius:3px;line-height:1.3;font-family:"SFMono-Regular",Consolas,"Liberation Mono",Menlo,Courier,monospace;font-size:.9em}#container{flex-grow:1}#modal{background:#fff}#aside{background:#f3f7fc}.content{padding:35px;padding-left:calc(35px + env(safe-area-inset-left));padding-right:calc(35px + env(safe-area-inset-right))}@media(min-width: 768px){#modal{display:flex}#main{width:50%;overflow:auto}#aside{width:50%;overflow:auto}}@media(min-width: 768px)and (min-height: 376px){body{background-color:#e4edf6;background-repeat:no-repeat;background-size:cover;background-position:center center}#container{display:flex;padding:24px;align-items:center;justify-content:center}#modal{height:100%;max-width:800px;max-height:525px;border-radius:4px;overflow:auto;box-shadow:0 25px 100px rgba(0,0,0,.5)}#aside{overflow:auto}}body{border:10px solid theme("colors.green.500")}@tailwind components;@tailwind utilities